@charset "UTF-8";
li,
ul {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  font-size: normal; }

*,
*::after,
*::before {
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: inherit; }

body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

button {
  background-color: white;
  border: 0;
  padding: 0; }

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff");
  font-weight: 700; }

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Light.woff") format("woff");
  font-weight: 300; }

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Regular.woff") format("woff");
  font-weight: 400; }

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Medium.woff") format("woff");
  font-weight: 500; }

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Italic.woff") format("woff");
  font-weight: 400;
  font-style: italic; }

body {
  font-family: 'Roboto';
  font-size: 16px;
  position: relative; }

.container {
  display: block;
  height: 100%;
  width: 1260px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%); }
  .container_nav {
    height: fit-content;
    min-height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .container_header {
    display: flex;
    align-items: center;
    height: fit-content;
    min-height: 127.5px; }
  .container_main-content {
    display: flex;
    justify-content: space-between; }

.main-content {
  height: 100%;
  margin-top: 60px; }

.aside {
  width: 270px; }
  .aside__categories {
    margin-bottom: 120px; }

.btn {
  padding: 20px 25px;
  background-color: #a94442;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  transition: background-color .5s; }
  .btn:hover {
    cursor: pointer;
    background-color: #333333; }
  .btn_cart {
    padding: 15px 20px; }

.cart {
  display: flex;
  align-items: center; }
  .cart__goods {
    display: block;
    margin-right: 5px;
    position: relative; }
    .cart__goods:hover .cart__icon {
      fill: #a94442;
      transition: fill .2s; }
  .cart__num {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(0, -90%);
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #a94442;
    color: #fff;
    text-align: center;
    line-height: 25px; }
  .cart__icon {
    width: 25px;
    height: 25px; }

.categories {
  text-transform: uppercase;
  width: 270px; }
  .categories__title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 16px; }

.category {
  padding: 20px 0;
  border-bottom: 1px solid #e9e9e9; }
  .category__link {
    display: flex;
    align-items: center; }
    .category__link:hover {
      color: #a94442;
      transition: color .2s; }
  .category__icon {
    width: 25px;
    height: 25px;
    fill: #a94442;
    margin-right: 10px; }
  .category__name {
    font-size: 16px;
    font-weight: 500; }

.contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end; }
  .contacts__row {
    display: flex;
    align-items: center; }
    .contacts__row:not(:last-of-type) {
      margin-bottom: 8px; }
  .contacts__icon {
    width: 16px;
    height: 18px; }
  .contacts__number {
    font-size: 14px;
    margin-left: 10px; }
  .contacts__descr {
    font-size: 12px;
    color: #777777; }

.content {
  width: 900px; }
  .content__slider {
    margin-bottom: 120px; }

.dots {
  display: flex;
  align-items: center; }
  .dots__item {
    width: 10px;
    height: 10px;
    background-color: #fff;
    border-radius: 50%;
    transition: width .2s, height .2s, background-color .5s; }
    .dots__item:hover {
      background-color: #333333; }
    .dots__item:not(:last-of-type) {
      margin-right: 25px; }
    .dots__item.active {
      width: 20px;
      height: 20px;
      border: 2px solid #fff;
      background-color: transparent; }
  .dots__link {
    display: block;
    width: 100%;
    height: 100%; }

.goods__title {
  margin-bottom: 30px; }

.goods__list {
  display: flex;
  flex-wrap: wrap; }

.goods__item {
  width: 23.5%;
  margin-bottom: 40px; }
  .goods__item:not(:nth-of-type(4n)) {
    margin-right: auto; }

.header {
  min-height: 127.5px; }
  .header__logo {
    margin-right: auto; }
  .header__cart {
    margin-left: 100px; }

.logo__link {
  display: block;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center; }

.logo__icon {
  width: 80px; }

.logo__text {
  margin-left: 10px;
  text-align: center; }

.logo__name {
  text-transform: uppercase;
  color: #000;
  font-size: 24px;
  font-weight: 500; }

.logo__descr {
  color: #777777;
  font-size: 14px; }

.mail__title {
  margin-bottom: 30px; }

.mail__descr {
  color: #777777;
  margin-bottom: 30px; }

.mail__input-area {
  position: relative;
  margin-bottom: 20px; }

.mail__icon {
  width: 20px;
  height: 20px;
  fill: #777777;
  position: absolute;
  top: 20px;
  left: 20px; }

.mail__input {
  width: 100%;
  padding: 20px 20px 20px 60px;
  color: #777777;
  font-size: 14px;
  outline: none;
  border: 1px solid transparent;
  transition: border-color .2s;
  background-color: #f6f6f8; }
  .mail__input:focus {
    border-color: #a94442; }

.menu {
  display: flex;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  transition: opacity .2s; }
  .menu__link {
    display: block;
    width: 100%;
    height: 100%;
    padding: 23.5px;
    position: relative; }
    .menu__link:after {
      content: "";
      display: block;
      position: absolute;
      height: 0;
      transition: height .2s;
      width: 100%;
      top: 0;
      left: 0;
      background-color: #fff; }
    .menu__link:hover {
      background-color: #333333; }
      .menu__link:hover:after {
        height: 5px;
        transition: height .2s; }

.nav {
  min-height: 70px;
  background-color: #a94442;
  height: fit-content; }
  .nav__search-row {
    margin-right: 20px; }

.price {
  font-size: 20px;
  color: #a94442; }
  .price::after {
    content: "₽";
    display: block;
    margin-left: 5px;
    font-size: 24px;
    color: #a94442; }

.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #e0e0e0;
  padding: 15px; }
  .product__img {
    max-height: 190px;
    margin-bottom: 20px;
    align-self: center; }
  .product__code {
    color: #777777;
    margin-bottom: 15px; }
  .product__cart-btn {
    display: block;
    width: fit-content; }
  .product__top-area {
    width: 100%;
    display: flex;
    flex-direction: column; }
  .product__bottom-area {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .product__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px; }
  .product__price {
    display: flex;
    justify-content: center;
    align-items: center; }

.search-row {
  display: none;
  opacity: 0;
  transition: opacity .2s;
  width: 100%;
  align-items: center;
  background-color: #f6f6f8; }
  .search-row__icon {
    width: 20px;
    height: 20px;
    margin-left: 25px;
    margin-right: 9px; }
  .search-row__input {
    padding: 16px;
    height: 100%;
    border: 0;
    outline: none;
    flex: 1;
    font-size: 14px;
    color: #777777;
    background-color: #f6f6f8; }
  .search-row__btn {
    text-transform: uppercase;
    font-size: 14px;
    color: #333333;
    font-weight: 500;
    background-color: #f6f6f8;
    padding: 16px 25px;
    outline: none;
    border: 0;
    position: relative; }
    .search-row__btn:before {
      content: "";
      position: absolute;
      display: block;
      width: 1px;
      height: 65%;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      background-color: #777777; }
    .search-row__btn:hover {
      color: #a94442;
      transition: color .2s;
      cursor: pointer; }
  .search-row__close {
    width: 20px;
    height: 20px; }

.search__link {
  display: block;
  width: 20px;
  height: 20px; }

.search__icon {
  width: 20px;
  height: 20px; }

#close {
  display: none;
  opacity: 0;
  transition: opacity .1s; }

#open {
  opacity: 1;
  transition: opacity .1s;
  fill: #fff; }

.slider {
  width: 900px;
  height: 570px;
  box-shadow: 0 10px 6px -6px #777;
  /* Rectangle 6: */
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  border-radius: 2px;
  position: relative; }
  .slider__dots {
    position: absolute;
    bottom: 45px;
    left: 50%;
    transform: translate(-50%, 0); }
  .slider__images {
    position: relative;
    height: 570px;
    overflow: hidden;
    position: relative; }
    .slider__images:after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #000;
      opacity: .3; }
  .slider__list {
    white-space: nowrap;
    font-size: 0;
    position: absolute;
    top: 0;
    left: 0;
    transition: left .5s; }
  .slider__item {
    width: 900px;
    height: 570px;
    overflow: hidden;
    display: inline-block;
    position: relative; }
  .slider__img {
    min-width: 900px; }
  .slider__tagline {
    position: absolute;
    left: 50px;
    top: 50%;
    transform: translate(0, 0%);
    z-index: 100;
    color: #fff; }
  .slider__text {
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 30px;
    max-width: 270px;
    white-space: normal; }
  .slider__button {
    padding: 20px;
    background-color: #a94442;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    transition: background-color .5s;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5); }
    .slider__button:hover {
      background-color: #333333; }

.title {
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 700; }
